//
// autocomplete.scss
//
 
  .autocomplete-suggestions {
    border: 1px solid $gray-200;
    background: lighten($light, 4%);
    cursor: default;
    overflow: auto;
    max-height: 200px !important;
    box-shadow: $box-shadow;
    strong {
      font-weight: bold;
      color: white;
    }
}
  
.autocomplete-suggestion {
  padding: 5px 10px;
  white-space: nowrap;
  overflow: hidden;
}

.autocomplete-no-suggestion {
  padding: 5px;
}

.autocomplete-selected {
  background: $primary;
  cursor: pointer;
  color: $white;
  
}

.autocomplete-group {
  padding: 5px;
  font-weight: $font-weight-medium;
  font-family: $font-family-secondary;
  strong {
    font-weight: bold;
    font-size: 16px;
    color: $gray-300;
    display: block;
  }
}